<template>
  <div class="todo-container">
    <h1>Todo</h1>
    <!-- add todo -->
    <div class="add-todo">
      <label for="todo">add a new todo</label>
      <input type="text" id="todo" v-model="content" @keyup.enter="postTodo" />
      <button class="add" @click="postTodo">Add!</button>
    </div>
    <!-- getting todos -->
    <div
      class="todos"
      v-for="todo in todos"
      :key="todo._id"
      :style="{ border: '2px solid green', margin: '5px', padding: '5px' }"
    >
      <span>{{ todo.date }}</span>
      <p>{{ todo.content }}</p>
      <div class="buttond">
        <button class="delete" @click="deleteTodo(todo._id)">delete</button>
        <button
          class="update"
          @click="
            getSpecificTodo(todo._id);
            updateShow = !updateShow;
          "
        >
          update
        </button>
      </div>
    </div>
    <!-- Updating a todo -->
    <div v-if="updateShow" class="update-layout">
      <div class="update">
        <label for="update-todo">update a todo</label>
        <input
          type="text"
          id="update-todo"
          v-model="updateTodo.content"
          @keyup.enter="
            update();
            updateShow = !updateShow;
          "
        />
        <button
          class="add"
          @click="
            update();
            updateShow = !updateShow;
          "
        >
          Update!
        </button>
      </div>
    </div>
  </div>
</template>

<script>
import todoServices from "@/todoServices";

export default {
  name: "TodoComponent",
  data() {
    return {
      todos: [],
      error: "",
      content: "",
      updateShow: false,
      updateTodo: {},
    };
  },
  async created() {
    // getting data from db after creating page
    // console.log(await todoServices.getTodos());
    this.todos = await todoServices.getTodos();
  },
  methods: {
    async postTodo() {
      // it will take its param from (this.)
      if (this.content != "") {
        await todoServices.postTodo(this.content);
      }

      // clear the input
      this.content = "";

      // add the posted todo to this.todos
      this.todos = await todoServices.getTodos();
    },
    async deleteTodo(id) {
      await todoServices.deleteTodo(id);

      this.todos = await todoServices.getTodos();
    },
    // get one to update it
    async getSpecificTodo(id) {
      this.updateTodo = await todoServices.getToUpdate(id);
    },
    async update() {
      await todoServices.updateTodo(
        this.updateTodo._id,
        this.updateTodo.content
      );

      this.todos = await todoServices.getTodos();
    },
  },
};
</script>

