import axios from "axios";
const url = "api/todos/";

class todoServices {
  // get
  static async getTodos() {
    const result = await axios.get(url);
    return result.data;
    // this will return a promise --> I'll use it in async await in vue component
  }

  // post
  static postTodo(text) {
    return axios.post(url, {
      content: text,
    });
  }

  // delete
  static deleteTodo(id) {
    return axios.delete(`${url}${id}`);
  }

  // get the one who need update
  static async getToUpdate(id) {
    const result = await axios.get(`${url}${id}`);
    return result.data;
  }
  // update
  static updateTodo(id, updatedText) {
    return axios.put(`${url}edit/${id}`, {
      content: updatedText,
    });
  }
}

export default todoServices;
